.App {
    margin: 0rem;
    font-family: Arial, Helvetica, sans-serif;
    

  }
  .background{
    background-image: url("https://i.ibb.co/t2JJfCp/imageedit-5-2216238736.png");
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: 300px 300px; /* Resize the background image to cover the entire container */
    background-color: lightcyan;
  
  }
  .heading{
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(55, 17, 194);
    padding-top: 10px;
    padding-bottom: 0px;
    image-resolution: 100px;
    
    background-color: lightblue;
    backface-visibility: visible;
    text-transform: uppercase;
  }

  .about{
    color: rgb(55, 17, 194);
    text-align: center;
    letter-spacing: initial;
    padding-top: 0px;
    padding-bottom: 0px;
    background-color: lightcyan;
    background-size: contain;
    
  
  }
  .footing{
    color: rgb(55, 17, 194);
    text-align: center;
    letter-spacing: inherit;
    padding-top: 0px;
    padding-bottom: 0px;
    background-color: lightblue;
  }

  .ul{
    list-style-type: none;
  }

.item1 { grid-area: header; }
/* .item2 { grid-area: menu; } */
.item3 { grid-area: main; }
/* .item4 { grid-area: right; } */
.item5 { grid-area: footer; }

.grid-container {
  display: grid;
  grid-template-areas:
    'header header header header header header'
    'main main main main main main'
    'footer footer footer footer footer footer';
  grid-gap: 0px;
  grid-column-gap: 0px;
  grid-row-gap: px;
  
  padding: 0px;
}

.grid-container > div {
  background-color: lightcyan;
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}
  .shine{
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    color: rgba(40, 17, 139, 0);
    background-size: 200% 100%;
    background-color: rgb(55, 17, 194);
    transition: 3s;
    cursor: grabbing;
    background-clip: text;
    -webkit-background-clip: text;
    background-image: linear-gradient(-76deg,transparent 0,transparent 5%,rgba(121, 128, 23, 0.986),rgba(242, 248, 150, 0.986),transparent 10%,transparent 100%);
  }

  .shine:hover{
    background-position: -140% 0;
  }